<template>
  <div id="songList">
    <div class="song-header">
      <div class="id nav">序号</div>
      <div class="name nav">歌名</div>
      <div class="songer nav">歌手</div>
      <div class="time nav">时长</div>
      <div class="album nav">专辑</div>
    </div>
 
      <song-item
        v-for="(item, index) in songs"
        :key="index"
        :index="index"
        :songId="item.id"
        :alId="item.al.id"
        :arId="item.ar[0].id"
        :songName="item.name"
        :alName="item.al.name"
        :arName="item.ar[0].name"
        :dt="item.format_dt"
      ></song-item>
  </div>

</template>
<script>
import tools from "../../tools/formatData.js";
import { req_song_detail } from "../../network/song/index.js";

import songItem from "../../components/item/songItem";
export default {
  name: "songList",
  components: {
    songItem,
  },
  props: {
    trackIds: Array,
    trackCount: Number,
    scroll: Number,
  },
  data() {
    return {
      songs: [],
      songNum: 40,
      song_page: 0,
      // 当前展示的条数
      currentShowNum: 0,
    };
  },
  watch: {
    trackIds: function() {
      // 对歌曲 id 进行处理
      let ids = this.disposeId();
      this.getsongdetail(ids);
    },
    songs: function() {
      this.songs.forEach((e, index, arr) => {
        let value = tools._msTos(e.dt);
        arr[index].format_dt = value;
      });
    },
    scroll: function() {
      if (this.currentShowNum < this.trackCount) {
        this.loadmove();
      }
    },
  },
  methods: {
    // 对 歌曲 id 进行处理
    disposeId() {
      let start = this.song_page * this.songNum;
      let end = start + this.songNum;
      end = end >= this.trackCount ? this.trackCount : end;
      if (end - start < this.songNum) {
        this.isLoadMove = false;
      } else {
        this.isLoadMove = true;
      }
      let arr = this.trackIds.slice(start, end);
      let str = tools._arrToString(arr);
      return str;
    },
    // 获取歌曲的信息（部分）
    getsongdetail(ids) {
      let params = {
        ids,
      };
      req_song_detail(this, params).then(res => {
        this.songs = this.songs.concat(res.songs);
        this.song_page++;
        this.currentShowNum = this.songs.length;
      });
    },
    // 加载更多
    loadmove() {
      let ids = this.disposeId();
      this.getsongdetail(ids);
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
#songList {
  .song-header {
    height: 30px;
    position: sticky;
    top: 0;
    @include flex();
    border: 1px solid #f5f5f5;
    background-color: #f5f5f5;
    .nav {
      @include font(18px, 30px, $colorC);
      text-align: center;
      border-right: 1px solid #ffffff;
      &:last-child {
        border: none;
      }
      &:first-child {
        width: 40px;
      }
      &:nth-child(n + 2) {
        flex: 1;
      }
    }
  }
  .item {
    height: 30px;
    padding-left: 12px;
    box-sizing: border-box;
    @include flex();
    @include font(14px, 30px, $colorB);
    &:nth-child(odd) {
      background-color: #ffffff;
    }
    &:nth-child(even) {
      background-color: #f7f7f7;
    }
    .item-d {
      cursor: pointer;
      text-align: center;
      @include font(14px, 30px, $colorB);
      @include fontHidden();
      &:first-child {
        width: 40px;
      }
      &:nth-child(n + 2) {
        flex: 1;
      }
    }
    .hover {
      &:hover {
        text-decoration: underline;
        color: $colorA;
        cursor: pointer;
      }
    }
  }
}
</style>